<template>
    <div id="search-home">
        <div class="options">
            <Button icon="md-arrow-back" @click="back">返 回</Button>
        </div>
        <img src="/image/logo.png" alt="资料检索工具">
        <div class="form">
            <AutoComplete
                clearable
                size="large"
                v-model="keyword"
                :data="keywordHistory"
                :filter-method="filterMethod"
                placeholder="输入需要搜索的关键字，多个关键字使用[ , ]隔开">
            </AutoComplete>
            <Button id="search-btn" icon="md-search" size="large" type="primary" @click="goSearch">聚合搜索</Button>
        </div>
        <div class="history">
            <Tag v-for="(kh, index) in keywordHistory"
                 v-if="index <= 49"
                 :key="index"
                 closable
                 size="large"
                 @click.native="clickKeywordHistory(kh)"
                 @on-close="removeKeywordHistory(kh)">
                {{kh}}
            </Tag>
        </div>
        <result-entry/>
    </div>
</template>

<script>
    import StringUtils from "@js/utils/string-utils";
    import {getSearchHistory, removeSearchHistory, saveSearchHistory} from "@js/utils/search-utils";
    import ResultEntry from "@cmp/result-entry";

    export default {
        name: "index",
        components: {ResultEntry},
        data() {
            return {
                keyword: null,
                keywordHistory: []
            }
        },
        methods: {
            back() {
                this.$router.push("/");
            },
            filterMethod(value, option) {
                if (StringUtils.isBlank(value) || StringUtils.isBlank(option)) {
                    return false;
                }
                return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
            },
            goSearch() {
                saveSearchHistory(this.keyword);
                if (StringUtils.isURL(this.keyword)) {
                    this.keywordHistory = getSearchHistory();
                    electronAPI.invoke({type: "openWindow", content: {url: this.keyword}})
                } else {
                    this.$router.push({
                        path: "engine",
                        query: {
                            t: new Date().getTime(),
                            keyword: this.keyword
                        }
                    })
                }
            },
            removeKeywordHistory(keyword) {
                this.keywordHistory = removeSearchHistory(keyword);
            },
            clickKeywordHistory(keyword) {
                this.keyword = keyword;
                this.goSearch();
            }
        },
        created() {
            this.keywordHistory = getSearchHistory();
            document.addEventListener('keydown', (event) => {
                if (event.keyCode === 13) {
                    // 执行回车键按下后的操作
                    event.preventDefault();
                    this.goSearch();
                }
            });
        }
    }
</script>

<style scoped lang="less">
    @import "~@style/variables.less";

    #search-home {
        width: 100%;
        text-align: center;
        padding: 10px 20%;
        margin-top: 150px;

        .options {
            position: absolute;
            top: 10px;
            left: 10px;
        }

        img {
            width: 100px;
            margin-bottom: 40px;
        }

        .form {
            display: flex;
            text-align: left;
        }

        .history {
            display: flex;
            flex-wrap: wrap;
            padding: @padding-size 0;

            .ivu-tag {
                cursor: pointer;
                white-space: nowrap;
            }
        }
    }

    #search-btn {
        margin-left: 10px;
    }
</style>
