<template>
    <div class="result-entry">
        <span ref="dest" class="tips-fixed" @click="showResultList">
            <Icon type="md-archive"/>
            打开检索列表 或 拖动超链接到此处
        </span>
        <Drawer :transfer="false" title="检索列表" :width="width" :closable="false" v-model="resultVisible">
            <result-list :show-back="false" ref="result-list"/>
        </Drawer>
    </div>
</template>

<script>
    import ResultList from "@cmp/result-list";
    import StringUtils from "@js/utils/string-utils";

    export default {
        name: "result-entry",
        components: {ResultList},
        data() {
            return {
                resultVisible: false
            }
        },
        computed: {
            width() {
                return window.screen.availWidth - 200;
            }
        },
        methods: {
            showResultList() {
                this.resultVisible = true;
                let $ref = this.$refs["result-list"];
                if ($ref) {
                    $ref.listResult();
                }
            }
        },
        mounted() {
            this.$refs.dest.ondrop = (evt) => {
                evt.preventDefault();
                const data = evt.dataTransfer.getData("text/plain");
                if (StringUtils.isNotBlank(data)) {
                    try {
                        let linkData = JSON.parse(data);
                        electronAPI.invoke({type: 'saveResult', content: linkData}).then(res => {
                            const {type, content} = res.content;
                            this.$Message[type]({content});
                        });
                    } catch (e) {
                        debugger
                        this.$Message.error("添加结果列表失败");
                    }
                }
            }

            document.ondragover = function (evt) {
                // 取消事件的默认行为
                return false;
            }
            document.ondrop = function (evt) {
                // 取消事件的默认行为
                return false;
            }
        }
    }
</script>

<style scoped lang="less">
    @import '~view-design/src/styles/index.less';

    .result-entry {
        .tips-fixed {
            width: 150px;
            position: fixed;
            z-index: 9;
            background: @primary-color;
            color: #fff;
            right: 0;
            top: 20%;
            padding: 10px;
            border-radius: 10px 0 0 10px;
            cursor: pointer;
        }
    }
</style>
