import {getStore, setStore} from "./store-utils.js";
import StringUtils from "./string-utils.js";

const key = "kw_his";
const maxLen = 200;

export function saveSearchHistory(keyword) {
    if (StringUtils.isBlank(keyword)) {
        return;
    }
    let keywordHistory = getSearchHistory();
    if (keywordHistory.length > maxLen) {
        keywordHistory.splice(0, maxLen / 2);
    }
    let findIndex = keywordHistory.findIndex(kw => kw === keyword);
    if (findIndex !== -1) {
        keywordHistory.splice(findIndex, 1);
    }
    keywordHistory.unshift(keyword);
    setStore({name: key, content: keywordHistory});
}

export function removeSearchHistory(keyword) {
    let keywordHistory = getStore({name: key}) || [];
    let findIndex = keywordHistory.findIndex(kw => kw === keyword);
    if (findIndex !== -1) {
        keywordHistory.splice(findIndex, 1);
    }
    setStore({name: key, content: keywordHistory});
    return keywordHistory;
}

export function getSearchHistory() {
    return getStore({name: key}) || [];
}
